<template>
  <div class="login">
    <h1>ÈúÄË¶ÅËæìÂÖ•‰Ω†ÁöÑÊ∑òÂè£‰ª§ËøõË°åÊ†áËØÜ</h1>
    <h3>Ê∑ªÂä†Â•ΩÂèãÂâçÔºåÈúÄË¶ÅËøõË°åÊ≥®ÂÜå</h3>

    <Field
        v-model="command"
        type="textarea"
        placeholder="ÈªèË¥¥ÊÇ®ÁöÑÊ∑òÂè£‰ª§Âëó~"
