<script>
import { Field, Loading } from 'vant'
import { ACTION_REGISTER } from '../store/actions.type';
import { mapState } from 'vuex'

export default {

  data () {
    return {
      command: ''
    }
  },

  components: { Field, Loading },

  computed: {
    taobaoCommandReg () {
      return /[\w\-_]+(\.[\w\-_]+)+([\w\-.,@?^=%&:/~+#]*[\w\-@?^=%&/~+#])?/
    },
    ...mapState(['isLoading'])
  },

  watch: {
    command: function (newVal) {
      if (newVal === '') return
      if (this.taobaoCommandReg.test(newVal)) {
        const link = this.taobaoCommandReg.exec(newVal)[0]
        console.log(link)
        this.$store.dispatch(ACTION_REGISTER, { link }).then(() => {
          this.$router.push('/')
        }).catch(() => {
          this.command = ''
        })

      } else {
        this.command = ''
        this.$dialog.alert({ title: 'Ë≠¶Âëä', message: 'Ëß£ÊûêÂè£‰ª§Â§±Ë¥•QAQ' })
      }

    }
  },

  metaInfo: () => ({
    title: 'Ê†áËØÜ',
    meta: [
      { // set meta
        name: 'keyWords',
        content: 'ÊÄßÊÑüÁ†ÅÂÜúÔºåÂú®Á∫øÁÇπËµû'
      },
      { charset: 'utf-8' },
      {
        name: 'viewport',
        content: 'width=device-width, initial-scale=2.0, maximum-scale=1.0, user-scalable=no'

      }
    ]

  })
}
</script>

